.header{
    display: -webkit-flex;
    display: flex;
    background: white;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 0px 40px;
    height: 80px;
    border-style: solid;
    border-bottom-width: 1px;
    border-top-width: 0;
    border-right-width: 0;
    border-left-width: 0;
}

.logo-nav {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}

.logo-container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.logo {
    width: 45px;
    height: 45px;
}

.nav-options {
    padding-left: 25px;
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-gap: 50px;
    list-style-type: none;
}

.mobile-option {
    display: none;
}

.option :hover {
    color: black;
}

.mobile-menu {
    display: none;
}

@media (max-width: 648px) {
    /*Mobile View */
    .header {
        padding: 0px 10px;
    }
    .logo {
        width: 45px;
        height: 45px;
    }
    .nav-options {
        display: -webkit-flex;
        display: flex;
        width: 100%;
        height: 180px;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 0;
        transition: all 0.5s ease;
        -webkit-flex-direction: column;
                flex-direction: column;
        list-style-type: none;
        grid-gap: 0px;
    }
    .nav-options.active {
        background: #808080;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
        -webkit-align-content: center;
                align-content: center;
        padding-left: 0px;
    }
    .menu-icon {
        width: 45px;
        height: 45px;
    }
    .option {
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
                align-items: center;
        -webkit-justify-content: center;
                justify-content: center;
        height: 10vw;
        padding: 30px 0px;
    }
    
}

.mobile-menu {
    display: block;
}

.app-footer {
    left: 0;
    bottom: 0;
    width: 100%;
    background: white;
    text-align: center;
    height: 8%;
    color: black;
    padding: 53px 0;
    border-top: 1px solid black;
    margin-top: 5%;
}

.app-footer__message {
    text-align: justify;
    font-weight: 300;
}

