#darkMode {
    background: transparent url(/movements-dark/static/media/moon.08c8560a.png) no-repeat center;
    background-size: 30px 30px;
    width: 45px;
    height: 45px;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    border: none;
    border-radius: 50%;
    transition: background-color 0.3s ease-in-out, -webkit-filter 0.3s ease-in-out;
    transition: background-color 0.3s ease-in-out, filter 0.3s ease-in-out;
    transition: background-color 0.3s ease-in-out, filter 0.3s ease-in-out, -webkit-filter 0.3s ease-in-out;
  }
  
  #darkMode:hover,
  #darkMode:focus {
    -webkit-filter: none;
            filter: none;
    background-color: black;
    cursor: pointer;
  }
  
  #darkMode.clicked {
    -webkit-filter: none !important;
            filter: none !important;
    background-color: black;
  }
  
.App {
  font-family: Montserrat;
  font-weight: 600;
  text-align: center;
}

a {
  text-decoration: none;
  color:black;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}



:root {
  --black: rgb(40,44,54);
  /* --white: rgb(236, 236, 236); */
}


body {
  font-family: "Ubuntu", sans-serif;
  background: var(--background-color);
  color: var(--text-color);
  font-weight: var(--font-weight);
  /* transition: background 0.3s ease-in-out, color 0.6s ease-in-out; */
}

body.light {
  --background-color: var(--white);
  --text-color: var(--black);
  --font-weight: 400;
 
}

body.dark {
  --background-color: var(--black);
  --text-color: var(--white);
  --font-weight: 500;
 
}
